










































import {getOrderList,payOrder} from 'common/js/requestUrl'
import {request,showToast} from 'common/js/common'
import order from 'static/img/icon/order.png'
import chetie from 'static/img/icon/chetie.png'
export default {
	data() {
		return {
			order,
			chetie,
			orderList: [],
			choosedItem: 10,
			page: 1,
			pageSize: 5,
			total: 0
		};
	},
	onShow(){
		this.getOrderList()
	},
	onReachBottom: function() {
		if( (this.page * this.pageSize) < this.total ){
			this.page += 1
			this.getOrderList();
		}
	},
	methods:{
		changeChoosed(index){
			this.choosedItem = index
			this.page = 1
			this.pageSize = 5
			this.total = 0
			// this.orderList = []
			this.getOrderList()
		},
		getOrderList(){
			request(getOrderList,{
				page: this.page,
				pageSize: this.pageSize,
				type: this.choosedItem
			},(res) => {
					if(res.result.data.length > 0){
						if(this.page == 1){
							this.orderList = res.result.data
						}else{
							this.orderList = this.orderList.concat(res.result.data)
						}
						this.total = res.result.total
					}else{
						if(this.page == 1){
							this.orderList = []
							this.total = 0
						}
						showToast('没有更多数据了')
					}
				}
			)
		},
		payOrders(order_id){
			request(payOrder,{
				order_id: order_id
				},(res) => {
					if(res.code != 200){
						showToast('付款失败');
					}
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: res.result.timeStamp,
						nonceStr: res.result.nonceStr,
						package: res.result.package,
						signType: res.result.signType,
						paySign: res.result.paySign,
						success: function (res) {
							console.log('success:' + JSON.stringify(res));
						},
						fail: function (err) {
							console.log('fail:' + JSON.stringify(err));
						}
					});
				}
			)
		}
	}
}
